body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
    --bg-green: "#ebf7d4";
    --main-green: "#517d0f";
    --bg-yellow: "#fff0b3";
    --main-yellow: "#936e10";
    --bg-grey: "#ebeff2";
    --main-grey: "#a0aebd";
    width: 100%
}

.guestTimeView {
    width: 100%;
    display: flex;
    margin: auto;
    padding: auto;
}

.meetingData {
    display: flex;
    flex-direction: column;
    margin: 15px 15px;
}

p {
    margin: 0;
}

.eventMetadata h6 {
    margin: 0;
    font-size: 1.125rem;
}

.schedulingTool {
    display: flex;
    flex-direction: column;
}

.newEntryRow {
    display: flex;
}

.newEntryRow p.nameText {
    font-weight: 500;
    font-size: 1.125rem;
}

.existingEntryRow {
    display: flex;
}

.existingEntryRow p.nameText {
    font-weight: 500;
    font-size: 1.125rem;
}

.timeColumns {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
    grid-auto-columns: minmax(90px, 1fr);
    grid-auto-flow: column;
    position: relative;
    will-change: auto;
}

p.date-header {
    margin: 0;
    font-weight: 700;
    font-size: 2rem;
    color: black;
}

p.day-month-header {
    margin: 0;
    font-weight: 500;
    font-size: 0.875rem;
    color: black;
}

.timeComponent p {
    margin: 0;
    text-align: center;
    white-space: nowrap;
    color: black;
}

